@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
html{box-sizing:border-box}*,*::after,*::before{box-sizing:inherit}body{margin:0;padding:0;line-height:1;font-family:"Roboto", sans-serif;color:#202020;background-color:#fafafa}.header{border-bottom:1px solid #ca2100;background-color:#db4c3f;box-shadow:0 1px 2px rgba(0,0,0,0.25);height:44px;width:100%}.header img{width:44px;margin-left:20px}.header nav{height:100%;max-width:923px;display:flex;align-items:center;margin:auto}.content{display:flex;max-width:923px;margin:auto}.sidebar{-webkit-user-select:none;-ms-user-select:none;user-select:none;width:266px;height:100vh;padding-top:74px;border:1px solid black;background-color:#f5e4e4}.sidebar div{margin-bottom:10px;cursor:pointer;padding:15px}.sidebar div.active{background:#ebf5d8}.sidebar div .icon{margin-right:10px}.tasks{background-color:#c6f8dc;height:100vh;width:calc(922px - 266px);padding:74px 30px 0}.tasks .add-task-btn{cursor:pointer}.tasks .add-task-btn .plus{color:#dd4b39;font-size:18px}.tasks .add-task-text{color:#545454;font-size:14px;margin-left:15px}.tasks .add-task-dialog{margin-top:10px}.tasks .add-task-dialog input{width:100%;border:1px solid #ddd;border-radius:3px;background-color:white;height:35px;padding-left:10px;padding-right:10px}.tasks .add-task-dialog .add-task-actions-container{display:flex;justify-content:space-between;margin-top:10px}.tasks .add-task-dialog .add-task-actions-container button{cursor:pointer;margin-right:5px;padding:10px 20px;border:none;border-radius:3px;background-color:transparent;font-weight:bold;color:gray}.tasks .add-task-dialog .add-task-actions-container .add-btn{background-color:#db4c3f;color:white;font-weight:bold}.tasks .add-task-dialog .add-task-actions-container .add-btn:disabled{background-color:gray}.tasks .task-item-container{margin-top:40px;padding:0 20px}.tasks .task-item-container .task-item{display:flex;justify-content:space-between}.tasks .task-item-container .task-item:not(:last-child){border-bottom:1px solid #ca2100}

